.spinner.lds-ring{position:fixed;top:50%;left:50%;margin-left:-32px;margin-top:-32px;width:64px;height:64px;z-index:10;border-radius:5px;background-color:#000}.spinner.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:51px;height:51px;margin:6px;border:6px solid #fff;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#fff transparent transparent transparent}.spinner.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.spinner.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.spinner.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.shelf-container{display:flex;flex-wrap:wrap;width:85%;min-height:600px}.shelf-container-header{width:100%;margin-bottom:10px}.shelf-container-header .products-found{float:left;margin:0;margin-top:8px}.shelf-container-header .sort{float:right}.shelf-container-header .sort select{background-color:#fff;outline:none;border:1px solid #ececec;border-radius:2px;margin-left:10px;width:auto;height:35px;cursor:pointer}.shelf-container-header .sort select:hover{border:1px solid #5b5a5e}.shelf-container .shelf-item{width:25%;position:relative;text-align:center;box-sizing:border-box;padding:10px;margin-bottom:30px;border:1px solid transparent;cursor:pointer}.shelf-container .shelf-item:hover{border:1px solid #eee}.shelf-container .shelf-item:hover .shelf-item__buy-btn{background-color:#eabf00}.shelf-container .shelf-item .shelf-stopper{position:absolute;color:#ececec;top:10px;right:10px;padding:5px;font-size:0.6em;background-color:#1b1a20;cursor:default}.shelf-container .shelf-item__thumb img{width:100%}.shelf-container .shelf-item__title{position:relative;padding:0 20px;height:45px}.shelf-container .shelf-item__title::before{content:"";width:20px;height:2px;background-color:#eabf00;position:absolute;bottom:0;left:50%;margin-left:-10px}.shelf-container .shelf-item__price{height:60px}.shelf-container .shelf-item__price .val b{font-size:1.5em;margin-left:5px}.shelf-container .shelf-item__price .installment{color:#9c9b9b}.shelf-container .shelf-item__buy-btn{background-color:#1b1a20;color:#fff;padding:15px 0;margin-top:10px;cursor:pointer;-webkit-transition:background-color 0.2s;transition:background-color 0.2s}

